	module.exports = {"data":{"wordpressPost":{"title":"Prediabetes, PoTS and Portuguese physicians: most read articles in June","content":"<p>Several new papers made our top ten this month including Mainous <em>et al</em>.’s study on the prevalence of prediabetes in England, and McDonald <em>et al.</em>’s paper on postural tachycardia syndrome predominantly affecting young women. Other popular papers were the recently published paper from Krüsi <em>et al</em>., which explores the effects of criminalisation and policing of sex buyers and a systematic review and network analysis from Cameron <em>et al</em>., comparing antithrombotic agents for the prevention of stroke and major bleeding in patients with atrial fibrillation.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Mainous <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/6/e005002.full\">Prevalence of prediabetes in England from 2003 to 2011: population-based, cross-sectional study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Zhang <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/4/e004470.full?rss=1\">Spatial analysis on human brucellosis incidence in mainland China: 2004–2010</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">McDonald <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/6/e004127.full\">Postural tachycardia syndrome is associated with significant symptoms and functional impairment predominantly affecting young women: a UK perspective </a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Cameron <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/6/e004301.full#aff-1\">Systematic review and network meta-analysis comparing antithrombotic agents for the prevention of stroke and major bleeding in patients with atrial fibrillation</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Krüsi <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/6/e005191.full\">Criminalisation of clients: reproducing vulnerabilities for violence and poor health among street-based sex workers in Canada—a qualitative study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Jenkins<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/2/e003505.full\">Effect of a 6-month vegan low-carbohydrate (‘Eco-Atkins’) diet on cardiovascular risk factors and body weight in hyperlipidaemic adults: a randomised controlled trial </a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Dahlen <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/5/e004551.full\">Rates of obstetric intervention and associated perinatal mortality and morbidity among low-risk women giving birth in private and public hospitals in NSW (2000–2008): a linked data population-based cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Rao <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/12/e004277.full\">Do healthier foods and diet patterns cost more than less healthy options? A systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Granja <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/6/e005026.full\">What keeps family physicians busy in Portugal? A multicentre observational study of work other than direct patient contacts</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Abu Dabrh <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/3/e003434.full?rss=1\">Health assessment of commercial drivers: a meta-narrative systematic review</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"23e9e7d5-7705-540a-b554-39766fb8324a"}}

